import React from "react";
import StatsActions from "../../../components/StatsActions";

type Props = { params: { code: string } };

export default async function Page({ params }: Props) {
  const code = String(params?.code ?? "");
  const base = process.env.NEXT_PUBLIC_BASE_URL || "http://localhost:3000";
  const url = `${base.replace(/\/$/, "")}/api/links/${encodeURIComponent(code)}`;

  const res = await fetch(url, { cache: "no-store" });
  if (!res.ok) {
    return (
      <div className="container">
        <h2>Stats</h2>
        <div className="p-4">
          <p>Link "{code}" not found.</p>
        </div>
      </div>
    );
  }

  const data = await res.json();

  return (
    <div className="container">
      <h2 style={{ marginTop: 0 }}>Stats — {data.code}</h2>

      <div style={{ padding: 16, borderRadius: 8, background: "#fff", boxShadow: "0 6px 18px rgba(2,6,23,0.04)" }}>
        <div style={{ marginBottom: 12 }}>
          <strong>Target URL</strong>
          <div style={{ marginTop: 6 }}>
            <a href={data.url} target="_blank" rel="noreferrer" style={{ color: "#2563eb", wordBreak: "break-all" }}>
              {data.url}
            </a>
          </div>
        </div>

        <div style={{ display: "flex", gap: 24, flexWrap: "wrap", marginBottom: 8 }}>
          <div>
            <strong>Total clicks</strong>
            <div>{typeof data.clicks === "number" ? data.clicks : 0}</div>
          </div>

          <div>
            <strong>Last clicked</strong>
            <div>{data.lastClicked ? new Date(data.lastClicked).toLocaleString() : "—"}</div>
          </div>

          <div>
            <strong>Created</strong>
            <div>{data.createdAt ? new Date(data.createdAt).toLocaleString() : "—"}</div>
          </div>
        </div>

        <StatsActions code={data.code} />
      </div>
    </div>
  );
}